#clock {
  display: flex;
  background-color: aqua;
  width: 60%;
  align-items: center;
  flex-direction: column;
}
#tittle {
  display: flex;
  background-color: yellow;
  justify-content: center;
  width: 30%;
}
#controls-div {
  display: flex;
  flex-direction: row;
  width: 80%;
  justify-content: space-around;
  background-color: antiquewhite;
}
#controls-play-div {
  display: flex;
  flex-direction: row;
  background-color: chocolate;
  width: 20%;
  justify-content: space-around;
  font-size: 40px;
}
